.sidebar .inside-right-sidebar {
  position: sticky;
  top: 0;
}

@keyframes customFade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/* SLIDER */
.is-style-is-threek-slider {
  width: 100%;
  height: auto;
  position: relative;
  overflow: hidden;
}
.is-style-is-threek-slider .wp-block-group__inner-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
}
.is-style-is-threek-slider .wp-block-group__inner-container.is-animating {
  transition: all 400ms;
}
.is-style-is-threek-slider .wp-block-group__inner-container figure {
  position: relative;
  flex: 1;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  aspect-ratio: 770/450;
}
.is-style-is-threek-slider .wp-block-group__inner-container figure a {
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}
.is-style-is-threek-slider .wp-block-group__inner-container figure img {
  position: relative;
  z-index: 1;
  width: 100% !important;
  height: auto !important;
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}

