/* SLIDER NAVIGATION */
.custom-slider-navigation {
  display: flex;
  justify-content: center;
  padding-top: 1rem;
  gap: 0.3rem;
}
.custom-slider-navigation button {
  cursor: pointer;
  width: 12px;
  height: 12px;
  border: none;
  background-color: white;
  outline: none !important;
  padding: 0;
  border: 1px solid #8E9BAA;
  border-radius: 50%;
}
.custom-slider-navigation button:hover, .custom-slider-navigation button.bullet-active {
  background-color: #8E9BAA;
}
.custom-slider-navigation button:first-child {
  margin-left: 0;
}
.custom-slider-navigation button:last-child {
  margin-right: 0;
}

img {
  border-radius: 4px;
}

